
.vue-swatches .swatch-square {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin: 4px 4px;
  border-radius: 4px;
  cursor: pointer;
}
.vue-swatches .swatch-circle {
  display: inline-block;
  width: 24px;
  height: 24px;
  margin: 4px 4px;
  border-radius: 50%;
  cursor: pointer;
}



